___CSS_LOADER_EXPORT___.push([module.id, `* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  list-style: none;
  outline: none;
  text-decoration: none;
}

body {
  background-color: transparent;
  font-family: "Roboto", sans-serif;
  font-size: 10px;
  margin: 0;
  background-color: #c2e2ea;
  margin: 5px 10px;
}

h1, h2, h3, li, input {
  color: #002d67;
}

h1 {
  font-size: 1.4rem;
}

h3 {
  font-size: 0.8rem;
}

li, input, a, .copyRights {
  border-radius: 0.15rem;
  font-size: 0.7rem;
}

header {
  margin-top: 2rem;
  padding-bottom: 1.2rem;
  border-bottom: 1px solid #72bbce;
}

section {
  margin-top: 2rem;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: stretch;
  gap: 0.4rem;
  white-space: nowrap;
  font-size: 0.8rem;
}

.btn {
  transition: all 0.5s;
  border-radius: 4px;
  font-size: 0.8rem;
  font-weight: 600;
  color: #002d67;
  background-color: #c2e2ea !important;
  border: 2px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1), 0 6px 6px rgba(0, 0, 0, 0.1);
  padding: 3px;
  cursor: pointer;
  text-align: center;
}
.btn:active {
  transform: scale(0.98);
}
.btn:focus {
  outline: 0;
}

.Side, form {
  flex: 1;
  gap: 0.7rem;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: stretch;
  padding: 0.5rem 0.3rem;
  background: #72bbce;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1), 0 6px 6px rgba(0, 0, 0, 0.1);
  border-radius: 0.2rem;
  height: 100%;
}
.Side .btn, form .btn {
  align-self: flex-end;
}

.Side .recentScores {
  background: #72bbce;
  gap: 0.2rem;
  display: flex;
  flex-direction: column;
  text-align: left;
  justify-content: center;
  overflow: auto;
  padding: 0.2rem;
  max-height: 270px;
}
.Side .recentScores li {
  border: 1px black solid;
  text-align: left;
  padding: 0.2rem;
}

.fstSect {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  gap: 0.5rem;
}

input {
  padding: 0.2rem;
}

footer {
  border-bottom: 1px solid #72bbce;
  position: relative;
  margin-top: 2rem;
  padding-bottom: 0.1rem;
  top: auto;
  bottom: 0.1rem;
}

@media (min-width: 768px) {
  body {
    font-size: 18px;
    padding-left: calc(12% - 80px);
    padding-right: calc(12% - 80px);
  }
  section {
    gap: 2rem;
    padding-left: calc(25% - 120px);
    padding-right: calc(25% - 120px);
  }
}
@media (min-width: 999px) {
  body {
    font-size: 23px;
  }
